<template>
<div  >
<!--  热力公司样本-->
<!--  {{this.tmpCaption}}-->
<!--  {{this.pri_detail.treeNodeOrgId}}-->
  <el-button  @click="sendAddMsg">增加树节点-同级</el-button>
  <el-button  @click="sendAddMsg2">增加树节点-子级</el-button>
  <el-button @click="sendModifyMsg">修改树节点</el-button>
  <el-button @click="sendDelMsg">删除树节点</el-button>
  <el-button @click="genLogicNo">生成UUID</el-button>
  <el-button style="float:right;right:100px;" type="primary" @click="showCb">检测</el-button>
  <query-valve-current-data-for-sa ref="query1"></query-valve-current-data-for-sa>

</div>
</template>

<script>

import Bus from "../../../utils/Bus.js"
import QueryValveCurrentDataForSa from "../../views/deviceManager/QueryValveCurrentDataForSa";

export default {
  name: "OrgInfoCompany",
  components:{'QueryValveCurrentDataForSa':QueryValveCurrentDataForSa},

  mounted(){
    console.info("Mounted_OrgInfoCompany:"+this.pri_detail.treeNodeOrgId);
    this.getQueryDetail();
  },
  created(){
    console.info("Created_OrgInfoCompany:"+this.pri_detail.treeNodeOrgId);
    console.info("Created_OrgInfoCompany.treeNodeIds:"+this.pri_detail.treeNodeIds);
    console.info("Created_OrgInfoCompany.treeNodeType:"+this.pri_detail.treeNodeType);
    console.info("Created_OrgInfoCompany.currentTreeNode:"+JSON.stringify(this.pri_detail.currentTreeNode));
    console.info("Created_OrgInfoCompany.cbInfo:"+JSON.stringify(this.pri_detail.cbInfo));
  },
  props: {

    pri_detail: {
      type: Object,
      default: function () {
        return {
          treeNodeCaptions:[],
          treeNodeIds:[],
          treeNodeOrgId:'',
          currentTreeNode:{},
          treeNodeType:[],
          cbInfo: {},
        }

      }
    }
  },
  watch:{
    pri_detail:function(newVal,oldVal){
      console.info("watch_OrgInfoCompany点击树节点查询【热力公司】新值:"+newVal.treeNodeOrgId+",旧值："+oldVal.treeNodeOrgId);
      console.info("watch_OrgInfoCompany点击树节点查询【热力公司】新值:"+newVal.treeNodeIds+",旧值："+oldVal.treeNodeIds);
      console.info("watch_OrgInfoCompany点击树节点查询【热力公司】当前树节点新值:"+JSON.stringify(this.pri_detail.currentTreeNode));
      console.info("watch_OrgInfoCompany点击树节点查询【热力公司】type值:"+this.pri_detail.treeNodeType);
      console.info("watch_OrgInfoCompany点击树节点查询【热力公司】type值:"+JSON.stringify(this.pri_detail.cbInfo));
      if(newVal.treeNodeOrgId!==oldVal.treeNodeOrgId){
       //查询

        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);

          /////////
          this.$refs.query1.treeNodeInfo.treeNodeOrgId = newVal.treeNodeOrgId;
          this.$refs.query1.treeNodeInfo.treeNodeCaptions = this.pri_detail.treeNodeCaptions;
          this.$refs.query1.reQueryValveCurrentData() ;
          // ////////

        }
        console.info(this.tmpCaption);

      }else if(newVal.cbInfo!==oldVal.cbInfo){
        // console.info("原楼栋信息["+JSON.stringify(oldVal)+"],新楼栋信息["+JSON.stringify(newVal)+"]，请处理。");
        console.info("楼栋信息发生变化，请处理。");
      }
    }
  },

  data(){

    return {
      tmpCaption: [],

    }
  },
  methods:{
    showCb(){
      let data = {};
      // console.info("返回对象："+JSON.stringify(  this.$refs.cb.showPublicInfo() ));
      this.$emit("parent_method", "", val => {
          console.info("showCb_obj:"+ JSON.stringify(val) );
          data = val;
        }
      );

    },
    genLogicNo(){
      console.info("生成UUID:"+this.customGlobal.genLogicNo("CM"));
    },
    sendAddMsg:function(){
      //热力公司增加树节点,1是同级，2是子级
      Bus.$emit('globalMsg',{curid:'0',target:{id:'100_1',caption:'长春高薪热力分公司100_1',type:1},method:'1',layer:'1'});
    },
    sendAddMsg2:function(){
      //热力公司增加树节点,1是同级，2是子级
      Bus.$emit('globalMsg',{curid:'100_1',target:{id:'100_2',caption:'长春净月热力分公司100_2',type:1},method:'1',layer:'2'});
    },
    sendModifyMsg:function(){
      //热力公司修改树节点
      Bus.$emit('globalMsg',{curid:'100_1_1_1',target:{id:'100_1_1_1',caption:'长春高新热力分公司A11',type:2},method:'2'});
    },
    sendDelMsg:function(){
      //热力公司修改树节点
      Bus.$emit('globalMsg',{curid:'100_2_1',target:{},method:'3'});
    },

    getQueryDetail: function(){

      if(this.pri_detail.treeNodeOrgId!==undefined){
        console.info("点击Tab标签页OrgInfoCompany查询【热力公司】,获取值:"+this.pri_detail.treeNodeOrgId);
        //查询
        this.tmpCaption = [];
        for(var i1=0;i1< this.pri_detail.treeNodeCaptions.length;i1++){
          this.tmpCaption[i1]= this.customGlobal.decrypt(this.pri_detail.treeNodeCaptions[i1],this.customGlobal.cryptoKey);
        }
        console.info(this.tmpCaption);
        //////////////
        this.$refs.query1.treeNodeInfo.treeNodeOrgId = this.pri_detail.treeNodeOrgId;
        this.$refs.query1.treeNodeInfo.treeNodeCaptions = this.pri_detail.treeNodeCaptions;
        this.$refs.query1.queryValveCurrentData() ;

      }else{
        console.info("当前没有选择任何组织机构ID，查询所有[热力公司]信息");
      }
    }
  }
}
</script>

<!--<style scoped>-->
<style >

</style>



